/*******************************
GENERAL BASE STYLES
*******************************/
body {
  background: #fff;
  font-family: helvetica, sans-serif;
  font-style: normal;
  font-weight: 300;
}

img {
  max-width: 100%;
}

a {
  color: #02c8d8 !important;
  text-decoration: underline;
}

a:hover {
  color: #a8a8a8 !important;
}

/*******************************
GENERAL BOOTSTRAP OVERIDE STYLES
*******************************/
.nav > li > a:hover,
.nav > li > a:focus {
  text-decoration: none;
  background: transparent;
}

.nav > li > a {
  padding: 10px 15px;
}

/*****************************
CUSTOM MOBILE ONLY HAMBURGER 
*******************************/
.navbar-toggle .icon-bar {
  position: relative;
  transition: all 500ms ease-in-out;
  background: #a8a8a8;
}

.navbar-toggle .icon-bar:nth-child(2) {
  top: 0px;
}

.navbar-toggle .icon-bar:nth-child(3) {
  top: 0px;
}

.navbar-toggle .icon-bar:nth-child(4) {
  top: 0px;
}

.navbar-toggle.active .icon-bar:nth-child(2) {
  top: 6px;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.navbar-toggle.active .icon-bar:nth-child(3) {
  top: 0px;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.navbar-toggle.active .icon-bar:nth-child(4) {
  display: none;
}

/*******************************
Bootstrap Mobile Toggle Overwrite
*******************************/
.navbar-default {
  background: transparent;
  border-bottom: 1px solid #a8a8a8 !important;
  border-top: 1px solid #a8a8a8 !important;
}

.navbar-default .navbar-toggle {
  border: none;
}

.navbar-default .navbar-toggle:focus, .navbar-default .navbar-toggle:hover {
  background: transparent;
}

.navbar-default .navbar-collapse {
  background: #fff;
  border-color: #a8a8a8 !important;
}

.navbar-default .navbar-collapse .nav li {
  border-bottom: 1px solid #a8a8a8;
  vertical-align: middle;
  margin: 0px auto;
  text-align: center;
  width: 100%;
}

.navbar-default .navbar-collapse .nav a {
  color: #2a2a2a !important;
  text-decoration: none;
}

.navbar-default .navbar-collapse .nav a:hover {
  color: #a8a8a8 !important;
}

.navbar-default .navbar-collapse .navbar-nav {
  margin: 0 -15px 7.5px -15px;
}

/*****************************
CUSTOM MOBILE SOCIAL ICONS
*******************************/
#mobile-stickynav #social-mobile {
  width: 200px;
  margin: 0 auto;
  padding: 6px 0;
}

#mobile-stickynav #social-mobile li {
  display: inline;
  padding: 2px 4px;
}

#mobile-stickynav #social-mobile li .fa {
  /* Mobile Font-Awesome ajustment */
  font-size: 25px;
}

header {
  min-height: 100px;
  width: 100%;
  border-bottom: 1px solid black;
}

.logo {
  text-align: center;
}

/* Extra small devices (phones, less than 768px) */
/* No media query since this is the default in Bootstrap */
/* Small devices (tablets, 768px and up) */
@media (min-width: 768px) {
  /*****************************
CUSTOM MOBILE SOCIAL ICONS
*******************************/
  #masonry {
    background: #fff;
    width: 100%;
  }
  .grid {
    background: #fff;
    width: 100%;
  }
  .grid div {
    float: left;
    width: 20%;
    height: 159px;
    margin: 0 2px 4px;
  }
  .grid .item-w2 {
    width: 40%;
  }
  .grid .item-h2 {
    height: 322px;
  }
  .grid .item-h3 {
    height: 485px;
  }
  .isotope .isotope-item {
    -webkit-transition-duration: 0.8s;
    -moz-transition-duration: 0.8s;
    transition-duration: 0.8s;
    -webkit-transition-property: -webkit-transform, opacity;
    -moz-transition-property: -moz-transform, opacity;
    transition-property: transform, opacity;
  }
  .grid img {
    display: block;
    width: 100%;
    height: 100%;
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 0.7s ease-in-out;
    -o-transition: all 0.7s ease-in-out;
    transition: all 0.7s ease-in-out;
  }
  .header-padding {
    padding: 5px 15px;
  }
  .main-nav {
    position: relative;
    top: 32px;
  }
  .main-nav li {
    padding: 5px 1px;
    font-size: .8em;
    font-weight: 500;
    color: #a8a8a8;
    display: inline-block;
    letter-spacing: 1px;
    text-transform: uppercase;
    vertical-align: top;
    position: relative;
  }
  .main-nav .seperator {
    width: 5px;
    padding: 0;
    margin-top: 18px;
    font-size: .9em;
  }
  .main-nav a {
    text-decoration: none;
    color: #2a2a2a !important;
  }
  .main-nav a:hover, .main-nav a:focus {
    color: #a8a8a8 !important;
  }
  .social {
    position: relative;
    top: 32px;
  }
  .social #social-icons {
    list-style: none;
    margin-top: 15px;
  }
  .social #social-icons li {
    display: inline-block;
    padding: 0px 5px;
  }
  .social #social-icons li .fa {
    font-size: 1.2em;
    padding: 0px;
  }
}

/* Medium devices (desktops, 992px and up) */
@media (min-width: 992px) {
  /********************************
Header, Navigation, Social Icons Styles
********************************/
  .main-nav {
    position: relative;
    top: 32px;
  }
  .main-nav li {
    padding: 5px;
    font-size: .9em;
    font-weight: 500;
  }
  .main-nav .seperator {
    width: 5px;
    padding: 0 16px;
    margin-top: 15px;
    font-size: 1.1em;
  }
  .nav > li > a {
    padding: 10px 10px;
  }
  .social {
    position: relative;
    top: 32px;
  }
  .social #social-icons {
    list-style: none;
    margin-top: 15px;
  }
  .social #social-icons li {
    display: inline-block;
    padding: 0px 5px;
  }
  .social #social-icons li .fa {
    font-size: 1.3em;
    padding: 5px;
  }
}

/* Large devices (large desktops, 1200px and up) */
